.label {
  display: flex;
}
.link {
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  color: #376fff;
  text-decoration: none;
  &Text {
  }

  &Icon {
    margin-left: 8px;
  }
}
.metrics {
  flex: 0 0 240px;
  max-width: 240px;
  width: 240px;
}

.preset {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  min-width: 1px;
  width: 500px;
  justify-content: flex-end;

  &Select {
    margin-right: 16px;
    flex: 0 0 280px;
    max-width: 280px;
    min-width: 1px;
  }

  &Action {
    flex-shrink: 0;
    min-width: 1px;
  }

  &Btn {
    svg {
      margin-right: 4px;
    }
  }
}
